.profile-container {
  display: flex;
  flex-direction: column;
  height: 80px;
}
.btns-container {
  text-align: right;
}

.mypage-btn {
  height: 25px;
  border-radius: 7px;
  color: #304ffe;
  font-weight: 500;
  width: 100px;
}
.mypage-btn:hover {
  background-color: #304ffe;
  color: #fff;
  transition: all 1s ease;
}

.logout-btn {
  height: 25px;
  border-radius: 7px;
  color: red;
  width: 100px;
  text-align: center;
  margin-top: 10px;
}

.logout-btn:hover {
  background-color: red;
  color: #fff;
  transition: all 1s ease;
}
