.notice-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  transform: translate(0, -5px);
}

.notice-sentence {
  font-weight: 500;
  color: black;
}
.notice-sentence:hover {
  color: #f50057;
  transition: all 0.5s ease;
}
.own-youtube {
  font-weight: 500;
  border-radius: 7px;
  background-color: white;
  color: #f50057;
  padding: 5px;
}
.own-youtube:hover {
  background-color: #f50057;
  color: white;
  transition: all 0.5s ease;
}
