.history-description {
  margin-top: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.reset-btn {
  margin-left: 15px;
  border-radius: 7px;
  background-color: red;
  color: white;
  font-weight: 500;
}

.reset-btn:hover {
  background-color: white;
  color: red;
  transition: all 1s ease;
}

.history-table-container {
  margin-top: 20px;
  height: 440px;
  overflow-x: hidden;
  overflow-y: auto;
}

.history-table {
  width: 720px;
  margin: auto;
}

.history-thead {
  color: black;
  font-size: large;
  font-weight: 500;
  height: 30px;
}

.history-task-column {
  width: 255px;
}
.history-category-column {
  width: 140px;
}
.history-format-column {
  width: 85px;
}

.history-due-column {
  width: 60px;
}

.history-studytime-column {
  width: 90px;
}
